.section.floorplan-stats {
    background: $purple_light;
}
.section.floorplan-stats::after {
    width: 0;
    height: 0;
    border-style: solid;
    margin-left: -26px;
    border-width: 26px 26px 0 26px;
    border-color: $purple_light transparent transparent transparent;
    position: absolute;
    top: 100%;
    left: 50%;
    display: block;
    content: ' ';
    z-index: 2;
}
.section.floorplan-stats .container {
    padding: 30px;
}
@media (min-width: $(tablet)) {
    .section.floorplan-stats .container {
        padding: 60px;
    }
}
.section.floorplan-stats ul {
    clear: both;
    *zoom: 1;
    background: $white;
    display: block;
    width: 100%;
    padding: 15px;
    text-align: center;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
}
.section.floorplan-stats ul:before {
    content: '';
    display: table;
}
.section.floorplan-stats ul:after {
    content: '';
    display: table;
    clear: both;
}
.section.floorplan-stats ul::before,
    .section.floorplan-stats ul::after {
    content: '';
    clear: both;
    display: block;
}
@media (min-width: $(desktop)) {
    .section.floorplan-stats ul {
        padding: 30px 60px;
    }
}
.section.floorplan-stats ul li {
    font-size: 18px;
    font-family: 'Open Sans';
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;
    width: 100%;
    color: $black;
}
@media (min-width: $(tablet)) {
    .section.floorplan-stats ul li {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 1/2 - (30px - 30px * 1/2));
    }
    .section.floorplan-stats ul li:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .section.floorplan-stats ul li:last-child {
        margin-right: 0;
    }
    .section.floorplan-stats ul li:nth-child(2n) {
        margin-right: 0;
        margin-left: auto;
    }
}
@media (min-width: $(desktop)) {
    .section.floorplan-stats ul li {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 1/3 - (30px - 30px * 1/3));
    }
    .section.floorplan-stats ul li:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .section.floorplan-stats ul li:last-child {
        margin-right: 0;
    }
    .section.floorplan-stats ul li:nth-child(3n) {
        margin-right: 0;
        margin-left: auto;
    }
}
.section.floorplan-stats ul li,
            .section.floorplan-stats ul li span {
    text-align: left;
}
.section.floorplan-stats ul li span {
    display: inline-block;
    font-weight: bold;
    float: left;
}
.section.floorplan-stats ul li i::before {
    color: $purple;
    font-size: 32px;
    margin-right: 5px;
}
.section.floorplan-stats a {
    display: block;
    background: $purple;
    width: 100%;
    color: $white;
    text-align: center;
    padding: 15px 30px;
    font-family: 'Open Sans';
    text-decoration: none;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 1px;
    -webkit-transition: all 0.25s;
    transition: all 0.25s;
    border-top: 1px solid $purple;
}
.section.floorplan-stats a:hover {
    color: $purple;
    background: $white;
}
